import './PhotosBar.css';
import React, { useContext } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import StoreApi from '../../Utils/StoreApi';
import { UseInfiniteScroll } from '../../Hooks/UseInfiniteScroll';

export default function PhotosBar({ btn2 }) {
	const { images, setBgImage, setBgColor } = useContext(StoreApi);
	const { count } = UseInfiniteScroll();

	const btn1 = () => {
		document.querySelector('.sideBar-bar.sideBar-bar-photos').style.width = '0';
	};

	return (
		<section className='sideBar-bar sideBar-bar-photos'>
			<header className='sideBar-head-photos'>
				<ChevronLeftIcon
					id='fa-angle-left'
					onClick={btn1}
				/>
				<h6 className='sideBar-header'>Photos</h6>
				<span
					onClick={btn2}
					className='sideBar-close-btn-photos'>
					
				</span>
			</header>
			<hr className='sideBar-hr' />
			<section className='sideBar-photos-colors-container overflow'>
				{images.slice(0, count).map((image, index) => {
					const btnImage = () => {
						setBgImage(`url(${image.full})`);
						setBgColor('');

						localStorage.setItem('storedBgPhotos', (document.querySelector('.Index').style.backgroundImage = `url(${image.full})`));
					};
					return (
						<div
							key={index}
							className='sideBar-wrap'>
							<LazyLoadImage
								className='sideBar-photos-picker'
								onClick={btnImage}
								src={`${image.thumb}`}
								alt={`url(${image.thumb})`}
							/>
							<a
								className='photographer'
								href={image.user.link}
								target='_blank'
								rel='noopener noreferrer'>
								{image.user.username}
							</a>
						</div>
					);
				})}
			</section>
		</section>
	);
}
